@import url(https://fonts.googleapis.com/css2?family=Quicksand&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Quicksand&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Acme&family=Catamaran&display=swap);
html{
  /* background-image: linear-gradient(to bottom right, rgb(81, 69, 248), rgb(111, 113, 228)); */
  background-image: linear-gradient(to bottom right, rgb(60, 212, 218), rgb(123, 248, 98));
  
  background-attachment: fixed;
  scroll-behavior: smooth;
  font-family: 'Quicksand', sans-serif;    
}

body::-webkit-scrollbar {
  width: 10px
} 

body::-webkit-scrollbar-thumb {   
background-color: rgb(0, 0, 0);   
}

.App {
  display: grid;
  grid-template-areas: 'menu container';

}

.menu{
  grid-area: menu;
  height: 100vh;
}

.content{
  padding-left: 100px;
  grid-area: auto;
  height: 100vh;
  width: 100vw;
}

body {
  margin: 0px;
  padding: 0px;
}




.conteiner-home{width:90vw;display:flex;justify-content:space-between;grid-gap:150px;gap:150px}.conteiner-home .text-home{text-align:justify;margin:0 auto}@media (max-width: 600px){.conteiner-home{display:flex;flex-direction:column-reverse}}

.scheduler{display:grid;margin-top:20px;grid-gap:30px;grid-template-columns:repeat(8, 1fr)}.scheduler .calendar-task{grid-column:1 / 2}.scheduler .calendar-task .task-preview{margin-top:20px;margin-bottom:20px}.scheduler .painel{grid-column:2 / 8;align-self:center}@media (max-width: 800px){.scheduler{display:flex;flex-direction:column}}

.calendar{width:300px;height:300px}.calendar .react-calendar{font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;box-shadow:0 0 0.2em #7e7d7d;border-radius:10px}.calendar .react-calendar__navigation button{font-size:20px}

.task-painel{width:80%;height:200px;margin:10px auto;border-radius:5px 0 0 5px;background-color:#fff;overflow-y:auto;overflow-x:unset;overflow-wrap:break-word;scroll-behavior:smooth}.task-painel h2{background-color:#d5d5d8;margin:0}.task-painel p{text-align:justify;padding:15px}.task-painel .icons-sup{text-align:right;display:flex;justify-content:space-between;margin:5px}.task-painel .icons-sup-right{text-align:right;margin:5px}.task-painel .icons-inf{text-align:right}.task-painel .icon{font-size:20px;padding:2px;border-radius:20px}.task-painel .done{color:#07b107;border:1px solid rgba(0,0,0,0);border-radius:100px}.task-painel .undone{color:black;text-align:left;border:1px solid rgba(0,0,0,0);border-radius:100px}.task-painel .delete{color:#db1616;border-radius:100px;border:1px solid rgba(0,0,0,0);margin-left:10px}.task-painel .update{color:#006eff;border-radius:100px;border:1px solid rgba(0,0,0,0);margin-left:10px}.task-painel .done:hover{border:1px solid #07b107}.task-painel .undone:hover{border:1px solid black}.task-painel .delete:hover{border:1px solid #db1616}.task-painel .update:hover{border:1px solid #006eff}.task-painel::-webkit-scrollbar{width:10px}.task-painel::-webkit-scrollbar-thumb{background-color:#1f08a0}

.painel{width:100%;height:auto;font-family:'Quicksand', sans-serif;box-shadow:0 0 0.2em #7e7d7d;border-radius:10px;text-align:center;margin-bottom:40px;background-color:rgba(255,255,255,0.212)}.painel .painel-main{height:85vh;margin:0px;display:grid;grid-template-columns:repeat(3, 1fr)}.painel .painel-main .painel-colunm{background-color:rgba(0,0,0,0.377);margin:10px;height:auto;border-collapse:collapse;overflow:auto;scroll-behavior:smooth;border-radius:10px}.painel .painel-main .painel-colunm-tasks{height:100px;display:grid;grid-template-columns:1fr}.painel .painel-main .painel-colunm p.type{margin:0;height:10%;font-family:'Indie Flower', cursive;background-color:rgba(36,230,188,0.452);font-family:'Righteous', cursive;font-size:30px;display:flex;justify-content:center;align-items:center;margin:auto}.painel-colunm::-webkit-scrollbar{width:10px}.painel-colunm::-webkit-scrollbar-thumb{background-color:#1f08a0}@media (max-width: 800px){.painel{margin:10px}.painel .painel-main{grid-template-columns:1fr;height:200vh}.painel .painel-main .painel-colunm{margin:5px;border:0.2px solid rgba(17,192,105,0.972);border-radius:0px}.painel .painel-main .painel-colunm p.type{height:40px;font-size:20px}}@media (max-width: 300px){.painel{margin:0px}}

.task-update{width:300px;background-color:white;font-family:"Segoe UI", Tahoma, Geneva, Verdana, sans-serif;box-shadow:0 0 0.2em #7e7d7d;border-radius:10px;margin:0 auto;margin-bottom:100px}.task-update .create-task-title{background-color:rgba(36,230,188,0.452);display:flex;justify-content:center;border-radius:10px 10px 0px 0px}.task-update .create-task-title h2{font-family:"Righteous", cursive;text-transform:uppercase;font-size:20px;margin:4px 0}.task-update .task-form{display:flex;flex-direction:column}.task-update .task-form input{border:none;border-bottom:2.5px solid #000000;margin-bottom:15px;font-size:14px}.task-update .task-form input:focus{box-shadow:0 0 0 0;outline:0}.task-update .task-form #form-title{margin-top:15px;align-self:center;width:50%}.task-update .task-form label{margin-left:10%;margin:0 0 5px 10%;font-size:15px;font-weight:600}.task-update .task-form #description{font-size:15px;width:90%;height:80px;align-self:center}.task-update .task-form .form-date{margin-top:15px}.task-update .task-form #date{width:50%;margin-left:10%}.task-update .task-form .btn{width:120px;height:40px;border:none;border-radius:5px;align-self:center;margin:10px}.task-update .task-form .btn.send{color:#ffffff;background:#006edc}.task-update .task-form .btn.cancel{color:#c00909;border:1px solid red;background-color:rgba(255,0,0,0)}.task-update .task-form .btn-send:hover{background:#0300ce}.task-update .task-form .btn-send:active{box-shadow:0 0 0.6em #006edc;background:#000000}

.task-preview{width:300px;background-color:white;font-family:"Segoe UI", Tahoma, Geneva, Verdana, sans-serif;box-shadow:0 0 0.2em #7e7d7d;border-radius:10px}.task-preview .create-task-title{background-color:rgba(36,230,188,0.452);display:flex;justify-content:center;border-radius:10px 10px 0px 0px}.task-preview .create-task-title h2{font-family:"Righteous", cursive;text-transform:uppercase;font-size:20px;margin:4px 0}.task-preview .task-form{display:flex;flex-direction:column}.task-preview .task-form input{border:none;border-bottom:2.5px solid #000000;margin-bottom:15px;font-size:14px}.task-preview .task-form input:focus{box-shadow:0 0 0 0;outline:0}.task-preview .task-form #form-title{margin-top:15px;align-self:center;width:50%}.task-preview .task-form label{margin-left:10%;margin:0 0 5px 10%;font-size:15px;font-weight:600}.task-preview .task-form #description{font-size:15px;width:90%;height:80px;align-self:center}.task-preview .task-form .form-date{margin-top:15px}.task-preview .task-form #date{width:50%;margin-left:10%}.task-preview .task-form .btn-send{width:120px;height:40px;border:none;border-radius:5px;box-shadow:0 0 0.6em #000;color:#ffffff;background:#006edc;align-self:center;margin:10px 0}.task-preview .task-form .btn-send:hover{background:#0300ce}.task-preview .task-form .btn-send:active{box-shadow:0 0 0.6em #006edc;background:#000000}

.perfil-page{color:whitesmoke;background-color:rgba(0,0,0,0.411);padding:20px;margin:20px;width:80%;box-shadow:0 0 0.5em #9ee7c5}.perfil-page .edite{background-color:#006edc;color:white}.perfil-page .remove{background-color:#d80606;color:white}.perfil-page .cancel{background-color:rgba(216,6,6,0.061);color:#000;border:2px solid black !important}.perfil-page .perfil-page-buttons{text-align:center}.perfil-page .custom-button{margin-top:30px;min-width:165px;width:auto;height:50px;font-size:15px;text-transform:uppercase;font-weight:bolder;border-color:whitesmoke;cursor:pointer;border-radius:10px;border:none;margin-left:20px}.perfil-page .custom-button:hover{background-color:rgba(245,245,245,0.26);color:black;border:2px solid black}

.perfil-update-form{color:black;background-color:rgba(0,0,0,0);height:auto;padding:10px;width:400px;margin:0 auto;text-align:center;box-shadow:0 0 1em rgba(247,247,247,0.76)}.perfil-update-form .perfil-update-buttons{text-align:center}.perfil-update-form form{display:flex;flex-direction:column}.perfil-update-form form .input{height:30px;font-size:15px;margin-top:20px;margin:10px}@media (max-width: 600px){.perfil-update-form{width:90%}}


.sign-in-and-sign-up{width:80vw;display:flex;flex-direction:row;justify-content:space-between;margin:20px auto;padding-right:100px}@media (max-width: 800px){.sign-in-and-sign-up{width:90%;flex-direction:column}.sign-in-and-sign-up *{margin-top:20px}}

.sign-in{width:380px;height:auto;padding:20px;color:whitesmoke;background-color:rgba(0,0,0,0.685);border-radius:20px;font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif}.sign-in .form{display:flex;flex-direction:column;height:100%}.sign-in .title{margin:10px 0}.sign-in .buttons{text-align:center}.sign-in .input{height:30px;font-size:15px}.sign-in .celula{display:flex;flex-direction:column;margin-top:40px}.sign-in .label{font-size:20px}.sign-in .custom-button{margin-top:60px;min-width:165px;width:auto;height:50px;letter-spacing:0.5px;line-height:50px;font-size:15px;background-color:rgba(0,0,0,0.712);color:white;text-transform:uppercase;font-weight:bolder;border-color:whitesmoke;cursor:pointer;border-radius:10px;box-shadow:0 0 0.5em whitesmoke;border:none}.sign-in .custom-button:hover{background-color:whitesmoke;color:black;box-shadow:0 0 2em black;border:1px solid black}@media (max-width: 500px){.sign-in .buttons{width:90%;flex-direction:column}}@media (max-width: 800px){.sign-in{width:90%}}

.sign-up{width:380px;height:auto;padding:20px;color:whitesmoke;background-color:rgba(0,0,0,0.685);border-radius:20px;padding-bottom:0;font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif}.sign-up .form{display:flex;flex-direction:column}.sign-up .title{margin:10px 0}.sign-up .buttons{text-align:center;margin-bottom:20px}.sign-up .input{height:30px;font-size:15px}.sign-up .celula{display:flex;flex-direction:column;margin-top:20px}.sign-up .label{font-size:20px}.sign-up .custom-button{margin-top:60px;min-width:165px;width:auto;height:50px;letter-spacing:0.5px;line-height:50px;font-size:15px;background-color:rgba(0,0,0,0.712);color:whitesmoke;text-transform:uppercase;font-weight:bolder;border-color:whitesmoke;cursor:pointer;border-radius:10px;box-shadow:0 0 0.5em whitesmoke;border:none}.sign-up .custom-button:hover{background-color:whitesmoke;color:black;box-shadow:0 0 2em black;border:1px solid black}@media (max-width: 800px){.sign-up{width:90%}}

.menu{position:fixed;font-family:'Acme', sans-serif;font-family:'Catamaran', sans-serif}.menu .pro-sidebar>.pro-sidebar-inner>.pro-sidebar-layout{color:white;background-color:#046e45}.menu .pro-sidebar .pro-menu.shaped .pro-menu-item>.pro-inner-item>.pro-icon-wrapper{background-color:rgba(0,0,0,0.404);border-radius:10px}.menu .menu-item{font-size:20px !important}.menu .menu-item:hover{background-color:rgba(0,0,0,0.404)}.menu .arrow-icon:hover{border-radius:100px;background-color:rgba(0,0,0,0.404)}.menu .closemenu,.menu .logotext{text-align:center}.menu .arrow-icon{padding:5px;font-size:30px}.menu .big-icon{color:whitesmoke;height:100%;align-items:center;justify-content:center}

